import h from '../../core/h'

const ResultPage = ({
    text = '暂无信息',       // 默认主文案
    subtext,                // 以更小的字体和更淡的颜色显示在主文案下方的副文案
    imgurl,                 // 图片链接
    buttons,                // 按钮列表
}) => {
    // 是否buttons是数组且有成员
    let isArrayOrMembers = Object.prototype.toString.call(buttons) == '[object Array]' && buttons.length > 0
    return <div class="ui-result-page">
        { !!imgurl ? <img src={imgurl} class="ui-result-page-img" /> : undefined }
        <div class="text">{text}</div>
        { !!subtext ? <div class="subtext">{subtext}</div> : undefined }
        { isArrayOrMembers ? <div class="ui-btn-wrap">
            {
                buttons.map((item, index) => {
                    return <button class={ item.type ? "ui-btn " + item.type : "ui-btn" } onclick={ item.handler }>
                        { item.text || '确定' }
                    </button>
                })
            }
        </div> : undefined }
    </div>
}

export {
    ResultPage,
}


